.Board {
  background: #efebe9;
  margin: 20px auto;
  min-width: 320px;
  position: relative;
  text-align: center;
}
.Cell {
  position: absolute;
  border-radius: 100%;
  border: solid 2px #a1887f;
  background: #bcaaa4;
  height: 20px;
  width: 20px;
}
.Cell.over {
	box-shadow: 0 0 20px 5px #ff6d00;
}
.Cell.green {
  border-color: #00c853;
}
.Cell.yellow {
  border-color: #ffd600;
}
.Cell.blue {
  border-color: #2962ff;
}
.Cell.red {
  border-color: #d50000;
}
.Piece {
  cursor: move;
  position: absolute;
  border-radius: 100%;
  height: 20px;
  width: 20px;
}
.Piece.dragging {
  opacity: 0.5;
}
.Piece.green { background: #1b5e20; }
.Piece.yellow { background: #ffd600; }
.Piece.blue { background: #0d47a1; }
.Piece.red { background: #b71c1c; }
.Piece.over {
	box-shadow: 0 0 20px 5px #ff6d00;
}
